import {
  getCeiling,
  getEquationSystemVals,
  getEven,
  getRound,
} from "@tanyoknits/shared";
import { ProjectProps } from "./utils/types";
import { needles } from "./needles";

const HEM_EXTRA_ROWS = 4;

const SHOULDER_WIDTH = 2; // Shoulder width in inch before ribbing
const ARM_INSET_DEPTH = 4.75; // Armhole depth in inch
const ARM_INSET_STS = 3;
const NECK_CURVE_DEPTH = 4; // Neckline curve depth in inch
const NECK_CURVE_STS = 6;

export default function BasicVest({ gauge, size }: ProjectProps) {
  const stsGauge = gauge.sts_count / 4;
  const rowGauge = gauge.row_count / 4;

  // Half body should be multiple of 4
  const bodySts = getCeiling(size.bust_circumference * stsGauge, 4);
  const hemIncSts = getRound(bodySts * 0.05, 4);

  const bodyRows = getEven(size.total_length * rowGauge);
  const hem = getEven(size.hem_length * rowGauge);

  // arm inset
  const armholeDepthRows = getEven(size.armhole_depth * rowGauge);
  const neckWidthSts = getEven(size.neck_width * stsGauge);
  const shoulderSts = getRound(SHOULDER_WIDTH * stsGauge);
  const armInsetSts = (bodySts / 2 - neckWidthSts - shoulderSts * 2) / 2;
  const armInsetRows = getEven(ARM_INSET_DEPTH * rowGauge);
  const armInsetStartRow = bodyRows - armholeDepthRows - 1;
  const armholeDecRows = getEquationSystemVals(
    [1, 1, armInsetSts - ARM_INSET_STS - (ARM_INSET_STS - 1)],
    [2, 4, armInsetRows - 4] // dec more than 1 sts twice - total 4 rows
  );

  // neckline
  const neckCurveRows = getEven(NECK_CURVE_DEPTH * rowGauge);
  const neckCurveSts =
    neckWidthSts / 2 -
    NECK_CURVE_STS -
    (NECK_CURVE_STS - 2) -
    (NECK_CURVE_STS - 4);
  const neckCurveDecRows = getEquationSystemVals(
    [1, 1, neckCurveSts],
    [2, 4, neckCurveRows - 6] // dec more than 1 sts twice - total 4 rows
  );
  const neckDepth = getEven(size.neck_depth * stsGauge);
  const neckStartRow = bodyRows - neckDepth + 1;

  const afterArmHoleSts =
    bodySts / 2 -
    ARM_INSET_STS * 2 -
    (ARM_INSET_STS - 1) * 2 -
    armholeDecRows[0] * 2 -
    armholeDecRows[1] * 2;
  return (
    <>
      <h1>Basic Vest</h1>
      <h2>Gauge</h2>
      <div>
        {stsGauge} sts / 1 inch, {rowGauge} rows / 1 inch
      </div>
      <h2>Key sizes</h2>
      <ul>
        <li>Total body: {bodySts} sts</li>
        <li>Total rows: {bodyRows} rows</li>
        <li>Armhole depth: {armholeDepthRows} rows</li>
        <li>Shoulder width: {shoulderSts} sts</li>
      </ul>
      <h2>Body - bottom up</h2>
      <h3>Hem + first part</h3>
      <ul>
        <li>
          With US size {needles[8].us} needle,
          <strong> CO {bodySts - hemIncSts} sts</strong>, *k2p2* rep *
          <strong>{hem + 1} rows</strong>
        </li>
        <li>Knit 1 row</li>
        <li>
          Change needle to US size {gauge.needle.us}, Knit till row{" "}
          {hem + HEM_EXTRA_ROWS}{" "}
        </li>
        <li>Knit, inc {hemIncSts} sts evenly, knit one more row</li>
      </ul>
      <h2>Back</h2>
      <h3>Armhole - back</h3>
      <div>Start armhole at row {armInsetStartRow}</div>
      <ul>
        <li>BO {ARM_INSET_STS * 2} at the beginning (left arm)</li>
        <li>K {bodySts / 2 - ARM_INSET_STS * 2} sts</li>
        <li>BO {ARM_INSET_STS * 2} at the beginning (right arm)</li>
        <li>Move the k sts in front to yarn holder</li>
      </ul>
      <ul>
        <li>
          K all till end, turn work, on WS BO, {ARM_INSET_STS - 1}, purl till
          end,
        </li>
        <li>
          Turn work, on RS, BO {ARM_INSET_STS - 1} (Total{" "}
          {bodySts / 2 - ARM_INSET_STS * 2 - (ARM_INSET_STS - 1) * 2} sts)
        </li>
        <li>Turn work, purl </li>
      </ul>
      <div>
        From row {armInsetStartRow + 4}, on each side dec every 2 rows{" "}
        {armholeDecRows[0]} times, then every 4 rows {armholeDecRows[1]} times;
        k2, ssk, k till last 4 sts, k2tog, k2
      </div>
      <ul>
        {Array.from({ length: armholeDecRows[0] }).map((_, i) => (
          <li key={i}>
            Row {armInsetStartRow + 4 + i * 2} (total{" "}
            {bodySts / 2 -
              ARM_INSET_STS * 2 -
              (ARM_INSET_STS - 1) * 2 -
              (i + 1) * 2}{" "}
            sts)
          </li>
        ))}
      </ul>
      <ul>
        {Array.from({ length: armholeDecRows[1] }).map((_, i) => (
          <li key={i}>
            Row {armInsetStartRow + 4 + armholeDecRows[0] * 2 + 2 + i * 4}{" "}
            (total{" "}
            {bodySts / 2 -
              ARM_INSET_STS * 2 -
              (ARM_INSET_STS - 1) * 2 -
              armholeDecRows[0] * 2 -
              (i + 1) * 2}{" "}
            sts)
          </li>
        ))}
      </ul>
      <h3>Neck back - left</h3>
      <div>
        Work short row at row {bodyRows - 5}, 3 times to make slope on shoulder
      </div>
      <ul>
        <li>
          At row {bodyRows - 5}, k{shoulderSts + 3}, BO until last{" "}
          {shoulderSts + 3} sts
        </li>
        <li>At row {bodyRows - 3}, BO 2 sts, turn work, work one WS</li>
        <li>
          At row {bodyRows - 1}, k2, ssk, k till end ({shoulderSts} sts
          remained)
        </li>
      </ul>
      <h3>Neck back - right</h3>
      <ul>
        <li>Start new yarn at row {bodyRows - 4}, work till end</li>
        <li>Work 2 rows, BO 2 sts on the start of WS,</li>
        <li>Next row, knit till last 4 sts, k2tog, k2</li>
        <li>Knit one more row than the </li>
      </ul>

      <h2>Front</h2>
      <h3>Neckline - right part</h3>
      <div>
        Start neckline at row {neckStartRow}, start with {afterArmHoleSts} sts
      </div>
      <ul>
        <li>
          Knit {(afterArmHoleSts - NECK_CURVE_STS * 2) / 2} sts, BO{" "}
          {NECK_CURVE_STS * 2}, cont pattern on the right front
        </li>
        <li>
          Row {neckStartRow + 2}, BO {NECK_CURVE_STS - 2} sts, cont pattern rows
        </li>
        <li>
          Row {neckStartRow + 4}, BO {NECK_CURVE_STS - 4} sts, cont pattern rows
        </li>
      </ul>
      <div>
        From {neckStartRow + 6}, dec 1 sts every 2 rows {neckCurveDecRows[0]}{" "}
        times, every 4 rows {neckCurveDecRows[1]} times - k2, ssk
      </div>
      <ul>
        {Array.from({ length: neckCurveDecRows[0] }).map((_, i) => (
          <li key={i}>Row {neckStartRow + 6 + i * 2}</li>
        ))}
      </ul>
      <ul>
        {Array.from({ length: neckCurveDecRows[1] }).map((_, i) => (
          <li key={i}>
            Row {neckStartRow + 6 + 2 * neckCurveDecRows[0] + 2 + i * 4}
          </li>
        ))}
      </ul>
      <h3>Neckline - left part</h3>
      <div>
        Do the same on the other side; BO on WS (one row after the previous
        part)
      </div>
      <div>BO as slope on RS (one row before the prev part)</div>
      <h2>Band</h2>
      <div>
        Neck: Pick up stitches: multiples of 4 + 2; add 2 more stitches on each
        front side, and 1 more on each back side; dec 1 sts on each point to
        make k2p2 rib pattern, at half point of total rows
      </div>
      <div>
        Arm: same, multiples of 4 + 2; dec 1 sts at each side on the bottom
      </div>
      <h2>Band</h2>
      <h3>Arm band</h3>
      <div>
        Pick up every 4 then 3 rows, then 4 rows along dec; (32, 25) * 2 + 8
      </div>
    </>
  );
}
