import {
  ChartRow,
  getBaseRowFromChartRow,
  getChartFromText,
  getMergedChart,
  getRightLeftFlippedChart,
  getTopDownFlippedChart,
  Motif,
  Stitch,
} from "../../utils";
import definition from "../definition";

const bottomRight = getChartFromText([
  "k:2 p:2 p:2 c4r p:6",
  "k:2 p:2 p:2 k:4 p:6",
  "k:2 p:2 c22rp k:2 p:6",
  "k:2 p:2 k:2 p:2 k:2 p:6 * 3",
  "k:2 p:2 k:2 c22rp p:6",
  "k:2 p:2 k:4 p:8",
]);
const bottomCross = getChartFromText([
  "k:2 p:2 k:2 * 4",
  "c222r",
  "k:2 p:2 k:2 * 3",
]);
const bottomPart = getMergedChart([
  bottomRight,
  bottomCross,
  getRightLeftFlippedChart(bottomRight),
]);

const centerRight = getChartFromText([
  "k:2 p:2 c22rph p:6 c4rh",
  "k:2 p:2 k:2 p:8 k:4",
  "k:2 p:2 k:2 p:6 c22rp k:2",
  "k:2 p:2 k:2 p:6 k:2 p:2 k:2",
  "c222l p:6 k:2 p:2 k:2",
  "k:2 p:2 k:2 p:6 k:2 p:2 k:2",
  "k:2 p:2 k:2 p:6 k:2 c22rp",
  "k:2 p:2 k:2 p:6 k:4 p:2",
  "k:2 p:2 k:2 p:6 c4r p:2",
  "k:2 p:2 k:2 p:6 k:4 p:2",
  "c222l p:4 c4rh c4lh",
  "k:2 p:2 k:2 p:4 k:8",
  "k:2 p:2 k:2 p:2 c22rp k:6",
  "k:2 p:2 k:2 p:2 k:2 p:2 k:6 * 3",
  "c222l p:2 k:2 c4rh k:4",
  "k:2 p:2 k:2 p:2 k:10",
  "k:2 p:2 k:2 p:2 c4r k:2 c4r",
  "k:2 p:2 k:2 p:2 k:10",
  "k:2 p:2 k:2 c4rh k:2 c22rph k:2",
  "k:2 p:2 k:10 p:2 k:2",
  "k:2 p:2 c4r k:2 c4r p:2 k:2",
  "k:2 p:2 k:10 p:2 k:2",
]);
const centerPart = getMergedChart([
  centerRight,
  getChartFromText(["p:2"]),
  getRightLeftFlippedChart(centerRight),
]);

const topRight = getChartFromText([
  "k:2 p:2 k:4 c22rph k:2 p:2",
  "k:2 p:2 k:6 p:2 k:2 p:2 * 3",
  "k:2 p:2 k:6 c22rp p:2",
  "k:2 p:2 k:8: p:4",
  "k:2 p:2 c22lph c22rph p:4",
  "k:2 p:4 k:4 p:6",
]);
const topCross = getChartFromText([
  "c222r",
  "k:2 p:2 k:2 * 5",
  "c222r",
  "k:2 p:2 k:2",
]);
const topChart = getMergedChart([
  topRight,
  topCross,
  getRightLeftFlippedChart(topRight),
]);

const bottomUpChart = [...bottomPart, ...centerPart, ...topChart];
const topDownChart = getTopDownFlippedChart(bottomUpChart);

const emptyStsPoint = [3, 7, 11, 14, 17, 20, 23, 26, 30, 34];
function getBaseStEquivRow(chartRow: ChartRow): (Stitch | null)[] {
  return getBaseRowFromChartRow(chartRow, true).map((sts, i) =>
    emptyStsPoint.includes(i) ? definition.empty : sts
  );
}

export const getMotif = (isTopDown: boolean = false): Motif => ({
  title: "Center Motif",
  chart: isTopDown ? topDownChart : bottomUpChart,
  stsCount: 38,
  stEquivCount: 26,
  baseStEquivRow: isTopDown
    ? getBaseStEquivRow(bottomUpChart[0])
    : getBaseStEquivRow(bottomUpChart[0]),
});
