import { getMotif } from "@tanyoknits/shared/src/stitches/patterns/Cable112Fusion";
import { ProjectProps } from "./utils/types";
import {
  BasePattern,
  Chart,
  ChartComponent,
  definition,
  FocusType,
  getBasePatternFromChart,
  getBaseRowFromChartRow,
  getCeiling,
  getChartFromBasePattern,
  getChartFromText,
  getEven,
  getFloor,
  getMergedChart,
  getNeededStsCountBasedOnSSE,
  getRawStsFromBaseRow,
  getRightLeftFlippedChart,
  getRound,
  getSlicedChartFromBasePattern,
  getStackedChart,
  getStsCountFromChartRow,
  MotifInfo,
  spliceRow,
} from "@tanyoknits/shared";
import { useState } from "react";

export default function CableBalaklava({ gauge, size }: ProjectProps) {
  const stsGauge = gauge.sts_count / 4;
  const rowGauge = gauge.row_count / 4;

  // Top panel - repeated motif
  const { chart, stsCount: repStsCount, stEquivCount } = getMotif(0);

  // Get actual sts count based on SSE, to match pattern, it's multiple of 4
  function getStsCount(stsCount: number, roundBy: number = 4): number {
    return getRound(
      getNeededStsCountBasedOnSSE(stsCount, repStsCount, stEquivCount),
      roundBy
    );
  }

  // Sts
  const topPanelSts = getStsCount(size.top_panel_width * stsGauge, 8); // must start & end with k3
  const tempTotalBodySts = getStsCount(size.body_circumference * stsGauge, 1); // temporarily needed to calculate the top panel side sts
  const neckIncSts = getStsCount(size.neck_curve_offset * stsGauge);
  const beforeNeckIncSts = tempTotalBodySts - neckIncSts * 2; // temporary total sts before neck inc
  const headIncSts = getStsCount(size.head_curve_length * stsGauge);
  const beforeHeadIncSts = beforeNeckIncSts - headIncSts * 2; // temporary total sts before head inc
  const topPanelSideSts = getRound((beforeHeadIncSts - topPanelSts) / 2, 8) + 2; // multiple of 4 + 2, end & start with k1
  const totalBodySts =
    topPanelSts + topPanelSideSts * 2 + headIncSts * 2 + neckIncSts * 2;
  const topPanelRows = getEven(
    ((topPanelSideSts * stEquivCount) / repStsCount / stsGauge) * rowGauge
  );

  // Rows
  // Total rows from pick up point on top panel
  const headRows = getEven(
    (size.head_height - size.top_panel_width / 2) * rowGauge
  );
  const totalRows = headRows + topPanelRows;
  const neckIncEveryXRows = getEven(
    (size.neck_curve_height * rowGauge) / neckIncSts
  );
  const neckIncStartRow = totalRows - neckIncEveryXRows * neckIncSts;

  /** chart by section */

  // Get motif per each section
  function getSectionChart(
    stsCount: number,
    rowRange: [number, number],
    sideChart: Chart = []
  ): Chart {
    //Motif side stitch (non-repeated) count is 22
    if (stsCount <= 22) {
      return [];
    }
    const repeatCount = getCeiling((stsCount - 22) / repStsCount);
    const motif = getMotif(repeatCount);
    const stackedMotifChart = Array.from({
      length: getCeiling(totalRows / motif.chart.length),
    }).flatMap(() => motif.chart);
    const slicedSts = (motif.stsCount - stsCount) / 2;
    const sideStsCount = getStsCountFromChartRow(sideChart[0]);
    const chart = getMergedChart(
      [
        sideChart,
        getSlicedChartFromBasePattern(
          getBasePatternFromChart(stackedMotifChart),
          rowRange,
          [slicedSts + sideStsCount, slicedSts + stsCount - sideStsCount]
        ),
        getRightLeftFlippedChart(sideChart),
      ],
      rowRange[1] - rowRange[0]
    );
    return chart;
  }

  // Motif
  const motifRows = chart.length;
  const topPanelMotifInfo: MotifInfo = {
    id: "green",
    stsCount: topPanelSts - 2,
    rowCount: motifRows,
  };
  const sidePanelMotifInfo: MotifInfo = {
    id: "red",
    stsCount: topPanelSideSts + 1,
    rowCount: motifRows,
  };
  const headIncMotifInfo: MotifInfo = {
    id: "blue",
    stsCount: headIncSts,
    rowCount: motifRows,
  };
  const neckIncMotifInfo: MotifInfo = {
    id: "blue",
    stsCount: neckIncSts,
    rowCount: motifRows,
  };

  // Top panel section
  const kChart = getChartFromText(["k"]);
  const topPanelSectionChart = getSectionChart(
    topPanelSts,
    [0, topPanelRows],
    kChart
  );
  const topPanelBasePattern: BasePattern = {
    baseChart: topPanelSectionChart.map((row) => getBaseRowFromChartRow(row)),
    motifList: [
      { ...sidePanelMotifInfo, stsCount: 1, startStsNum: 0 },
      { ...topPanelMotifInfo, startStsNum: 1 },
      { ...sidePanelMotifInfo, stsCount: 1, startStsNum: topPanelSts - 1 },
    ],
  };

  // Mid section with side pick up
  const bodySideChart = getChartFromText(["k p:2"]);
  const midFullSectionChart = getSectionChart(
    topPanelSts + topPanelSideSts * 2 + headIncSts * 2,
    [topPanelRows, neckIncStartRow],
    bodySideChart
  );
  const midSectionBasePattern = getBasePatternFromChart(midFullSectionChart);
  midSectionBasePattern.baseChart.forEach((row, i) => {
    if (i < 2) {
      // first two rows without inc
      spliceRow(row, 0, topPanelSideSts + neckIncSts, [
        ...getRawStsFromBaseRow(row)
          .map((s) => definition[s])
          .slice(0, topPanelSideSts),
        definition.p,
        ...Array(neckIncSts).fill(definition.empty),
      ]);
      spliceRow(
        row,
        topPanelSideSts + neckIncSts + topPanelSts - 1,
        topPanelSideSts + neckIncSts,
        [
          ...Array(neckIncSts).fill(definition.empty),
          definition.p,
          ...getRawStsFromBaseRow(row)
            .map((s) => definition[s])
            .slice(
              topPanelSideSts + neckIncSts + topPanelSts - 1,
              topPanelSideSts + neckIncSts * 2 + topPanelSts
            ),
        ]
      );
    } else if (i < 2 + neckIncSts) {
      // inc every row on both sides
      spliceRow(row, topPanelSideSts + i, neckIncSts - i + 1);
      spliceRow(row, topPanelSideSts + i - 1, 1, definition.m1l);
      spliceRow(
        row,
        topPanelSideSts + neckIncSts + topPanelSts - 1,
        neckIncSts - i + 1
      );
      spliceRow(
        row,
        topPanelSideSts + neckIncSts * 2 + topPanelSts - i,
        1,
        definition.m1r
      );
    }
  });
  midSectionBasePattern.motifList = [
    { ...sidePanelMotifInfo, startStsNum: 0 },
    { ...headIncMotifInfo, startStsNum: topPanelSideSts + 1 },
    { ...topPanelMotifInfo, startStsNum: topPanelSideSts + headIncSts + 1 },
    {
      ...headIncMotifInfo,
      startStsNum: topPanelSideSts + headIncSts + topPanelSideSts - 3,
    },
    {
      ...sidePanelMotifInfo,
      startStsNum: topPanelSideSts + headIncSts * 2 + topPanelSideSts - 3,
    },
  ];
  midSectionBasePattern.motifOffset =
    getCeiling(topPanelRows, motifRows) - topPanelRows;

  // Bottom section with neck inc
  const bottomSideChart = getChartFromText(["k p:2 k:2"]);
  const bottomFullSectionChart = getSectionChart(
    totalBodySts,
    [neckIncStartRow, totalRows],
    bottomSideChart
  );
  const bottomSectionBasePattern = getBasePatternFromChart(
    bottomFullSectionChart
  );
  const NECK_OFFSET = 4;
  bottomSectionBasePattern.baseChart.forEach((row, i) => {
    const stsCount = getFloor(i / neckIncEveryXRows) + 1;
    spliceRow(row, NECK_OFFSET + stsCount, neckIncSts - stsCount);
    spliceRow(
      row,
      totalBodySts - NECK_OFFSET - neckIncSts,
      neckIncSts - stsCount + 1
    );
    if (i % neckIncEveryXRows === 0) {
      spliceRow(row, NECK_OFFSET + stsCount - 1, 1, definition.m1l);
      spliceRow(
        row,
        totalBodySts - stsCount - NECK_OFFSET + 1,
        1,
        definition.m1r
      );
    }
  });
  bottomSectionBasePattern.motifList = [
    { ...sidePanelMotifInfo, stsCount: NECK_OFFSET, startStsNum: 0 },
    { ...neckIncMotifInfo, startStsNum: NECK_OFFSET },
    {
      ...sidePanelMotifInfo,
      stsCount: topPanelSideSts + headIncSts - NECK_OFFSET + 1,
      startStsNum: NECK_OFFSET + neckIncSts,
    },
    {
      ...topPanelMotifInfo,
      startStsNum: neckIncSts + topPanelSideSts + headIncSts + 1,
    },
    {
      ...sidePanelMotifInfo,
      stsCount: topPanelSideSts + headIncSts - NECK_OFFSET + 1,
      startStsNum: neckIncSts + topPanelSideSts + headIncSts + topPanelSts - 1,
    },
    {
      ...neckIncMotifInfo,
      startStsNum: totalBodySts - NECK_OFFSET - neckIncSts,
    },
    {
      ...sidePanelMotifInfo,
      stsCount: NECK_OFFSET,
      startStsNum: totalBodySts - NECK_OFFSET,
    },
  ];
  bottomSectionBasePattern.motifOffset =
    getCeiling(neckIncStartRow, motifRows) - neckIncStartRow;

  const stackedChart = getStackedChart([
    getChartFromBasePattern(topPanelBasePattern),
    getChartFromBasePattern(midSectionBasePattern),
    getChartFromBasePattern(bottomSectionBasePattern),
  ]);

  const [focus, setFocus] = useState<FocusType>([0, null]);

  return (
    <>
      <h1>Cable Balaklava</h1>
      <div>
        <h2>Gauge</h2>
        <div>
          {stsGauge} sts / 1 inch, {rowGauge} rows / 1 inch
        </div>
        <h2>Key sizes</h2>
        <ul>
          <li>Top panel: {topPanelSts} sts</li>
          <li>Top panel rows: {topPanelRows} rows</li>
          <li>Total body sts: {totalBodySts} sts</li>
          <li>Body rows: {headRows} rows, after panel</li>
        </ul>
        <h2>Repeated motif</h2>
        <ul>
          <li>
            SSE: {stEquivCount} sts / {getRound(stEquivCount / stsGauge, 0.25)}{" "}
            inch
          </li>
        </ul>
        <ChartComponent chart={chart} direction="flat" size="M" />
      </div>
      <div>
        <h2>Chart</h2>
        <ChartComponent
          chart={stackedChart}
          direction="flat"
          focus={focus}
          setFocus={setFocus}
          isOnlyRowClickable
          showStsCountPerRow
          size="S"
        />
      </div>
      <div>
        <h2>Top panel</h2>
        <ul>
          <li>
            Row 1: k,{" "}
            <span style={{ backgroundColor: "var(--oc-green-2)" }}>
              work chart
            </span>
            , k ({topPanelSts} sts)
          </li>
          <li>Row 2: k, work chart, p </li>
          <li>Total {topPanelRows} rows</li>
        </ul>
      </div>
      <div>
        <h2>Body</h2>
        <div>Pick up along the panel and make body</div>
        <ul>
          <li>
            Row {topPanelRows + 1} (RS):{" "}
            <span style={{ backgroundColor: "var(--oc-red-2)" }}>
              Pick up {topPanelSideSts} sts
            </span>{" "}
            on one side, (top panel){" "}
            <span style={{ backgroundColor: "var(--oc-green-2)" }}>p1</span>,
            pm,{" "}
            <span style={{ backgroundColor: "var(--oc-green-2)" }}>
              work chart
            </span>{" "}
            from next sts to last 1 sts, pm,{" "}
            <span style={{ backgroundColor: "var(--oc-green-2)" }}>p1</span>,
            pick up{" "}
            <span style={{ backgroundColor: "var(--oc-red-2)" }}>
              {topPanelSideSts} sts
            </span>
          </li>
          <li>
            Row {topPanelRows + 2} (WS): P1, work chart to marker, sm, work
            chart to marker, sm, k2, work chart to last 1 st, p1
          </li>
          <li>Total {topPanelSts + topPanelSideSts * 2} sts</li>
        </ul>
        <div>
          Continue shape head:{" "}
          <span style={{ backgroundColor: "var(--oc-blue-2)" }}>
            Inc total {headIncSts * 2} sts; do following 4 rows total{" "}
            {headIncSts / 4} times - inc 1 sts in every row
          </span>
        </div>
        <ul>
          <li>
            Row {topPanelRows + 3} (RS): k1, p2, work chart to marker, M1R, sm,
            work chart to marker, sm, M1L, p2, work chart to last 1 sts, k1
          </li>
          <li>
            Row {topPanelRows + 4} (WS): p1, k2, work chart to 1 sts before
            marker, p1, M1pL, sm, work chart to marker, sm, M1pR, p1, work
            chart, to last 1 sts, p1
          </li>
          <li>
            Row {topPanelRows + 5}: same as row {topPanelRows + 3} but do p at
            M1
          </li>
          <li>
            Row {topPanelRows + 6}: same as row {topPanelRows + 4} but do k at
            M1
          </li>
          <li>
            total {topPanelSts + topPanelSideSts * 2 + headIncSts * 2} sts
          </li>
        </ul>
        <div>
          <span style={{ backgroundColor: "var(--oc-blue-2)" }}>
            Inc {neckIncSts} sts along one face side
          </span>{" "}
          - pm after 7 sts at each side to beg inc
        </div>
        <ul>
          <li>
            Row {neckIncStartRow + 1}: k3, p2, k2, sm, M1L, work chart to
            marker, M1R, sm, k2, p2, k3
          </li>
          <li>Row {neckIncStartRow + 5}: rep prev 4 rows</li>
          <li>
            Rows {neckIncStartRow + 9}: k3, p2, k2, sm, M1pL, work chart, M1pR,
            sm, k2, p2, k3
          </li>
          <li>Rows {neckIncStartRow + 13}: rep prev 4 rows</li>
          <li>Rep above 16 rows, total {neckIncSts / 4} times</li>
          <li>
            total {totalBodySts} sts, till row {totalRows}
          </li>
        </ul>
      </div>
      <div>
        <h2>Yoke</h2>
        <div style={{ color: "red" }}>TODO</div>
      </div>
    </>
  );
}
