import {
  getChartFromText,
  getMergedChart,
  getTopDownFlippedChart,
} from "../../utils";

type ThinCableSide = "right" | "left";

const thinCable = getChartFromText([
  "p k p:2 k p * 2",
  "c6lpkp",
  "p k p:2 k p * 5",
]);
const thickCable = getChartFromText(["k:6 * 2", "c6r", "k:6 * 5"]);

const p = getChartFromText(["p"]);

const getChart = (thinCableSide: ThinCableSide) => {
  return thinCableSide == "right"
    ? getMergedChart([thinCable, p, thickCable])
    : getMergedChart([thickCable, p, thinCable]);
};

export const getMotif = (
  isBottomUp: boolean = false,
  thinCableSide: ThinCableSide = "right"
) => {
  const chart = getChart(thinCableSide);
  return {
    title: "Horseshoe Cable",
    chart: isBottomUp ? getTopDownFlippedChart(chart) : chart,
    stsCount: 13,
    stEquivCount: 10,
  };
};
