import {
  getCeiling,
  getEquationSystemVals,
  getEven,
  getRound,
} from "@tanyoknits/shared";
import { ProjectProps } from "./utils/types";

const COCO_STS = 4;
const INSET_WIDTH = 1;
const INSET_DEPTH = 4;
const BACK_OFFSET = 1.5; // inch from COCO to shoulder top
const ARM_INSET_STS = 2;
const SHOULDER_STRAIGHT_LENGTH = 2;
const SHOULDER_SHAPE_WIDTH = 1;
const SHOULDER_SHAPE_DEPTH = 2;

export default function MegaBraidVest({ gauge, size }: ProjectProps) {
  const stsGauge = gauge.sts_count / 4;
  const rowGauge = gauge.row_count / 4;

  // Basic sts/rows
  const bodySts = getEven(size.bust_circumference * stsGauge);
  const shoulderSts = getRound(size.shoulder_width * stsGauge);
  const neckWidthSts = getEven(
    (size.neck_width - SHOULDER_STRAIGHT_LENGTH) * stsGauge
  );
  const bodyRows = getEven(size.total_length * rowGauge);

  // CO - upper back
  const neckBackWidthSts = neckWidthSts - size.band_length * stsGauge * 2;
  const coSts = neckBackWidthSts + COCO_STS * 2;
  const upperBackSts = bodySts / 2;
  const upperBackRows = getCeiling((upperBackSts - coSts) / 2, 4);
  const upperBackTotalInc = upperBackSts - (coSts + 4);

  // Dec for sleeve line
  const backDecSts = INSET_WIDTH * stsGauge;
  const backDecRows = getEquationSystemVals(
    [1, 1, backDecSts],
    [
      2,
      4,
      getRound((size.armhole_depth - BACK_OFFSET - INSET_DEPTH) * rowGauge) -
        upperBackRows,
    ]
  );
  const afterDecRows = upperBackRows + backDecRows[0] * 2 + backDecRows[1] * 4;

  // Arm inset
  const armInsetRows = getEven(INSET_DEPTH * rowGauge);
  const armInsetStraightRows = getEven((armInsetRows / 3) * 2);
  const armholeIncRows = getEquationSystemVals(
    [1, 1, backDecSts - ARM_INSET_STS],
    [2, 4, armInsetRows - armInsetStraightRows] // dec more than 1 sts twice - total 4 rows
  );
  const totalUpperBackRows =
    afterDecRows +
    armInsetStraightRows +
    armholeIncRows[0] * 2 +
    armholeIncRows[1] * 4;

  // Front shoulder
  const pickupSts = getEven(upperBackTotalInc / 2) + 2;
  const shoulderStraightRows = getEven(SHOULDER_STRAIGHT_LENGTH * rowGauge);
  const shoulderShapeSts = getRound(SHOULDER_SHAPE_WIDTH * stsGauge);
  const shoulderShapeRows = getRound(SHOULDER_SHAPE_DEPTH * rowGauge);
  const quarterBodySts = bodySts / 4 - backDecSts;
  const neckLineRows = getEquationSystemVals(
    [1, 1, quarterBodySts - pickupSts],
    [4, 6, getEven(size.neck_depth * rowGauge) - shoulderShapeRows] // dec more than 1 sts twice - total 4 rows
  );

  // Armhole offset due to Coco
  const armholeRows = getEven(size.armhole_depth * rowGauge);
  const frontRows = armholeRows * 2 - totalUpperBackRows;
  const armInsetStartRow =
    totalUpperBackRows - afterDecRows - armInsetStraightRows + 1;

  // Lower body
  const lowerBodyRows = getEven(
    (size.total_length - size.armhole_depth - size.hem_length) * rowGauge
  );
  return (
    <div>
      <h1>Basic Vest</h1>
      <h2>Gauge</h2>
      <div>
        {stsGauge} sts / 1 inch, {rowGauge} rows / 1 inch
      </div>
      <h2>Key sizes</h2>
      <ul>
        <li>Total body: {bodySts} sts</li>
        <li>Total rows: {bodyRows} rows</li>
        <li>Shoulder width: {shoulderSts} sts</li>
        <li>Neck width: {neckWidthSts} sts</li>
      </ul>
      <h2>Upper back</h2>
      <div>
        <strong>CO {coSts}</strong> sts, p 1 row (WS)
      </div>
      <ul>
        <li>
          Row 1 (RS): K{COCO_STS}, M1L, k to last {COCO_STS} sts, M1R, k
          {COCO_STS} (2 sts inc'd)
        </li>
        <li>
          Row 2 (WS): P{COCO_STS}, M1R-p, p to last {COCO_STS} sts, M1L-p, p
          {COCO_STS} (2 sts inc'd, total {coSts + 4} sts)
        </li>
        <li>
          Repeat row 1-2 for {upperBackTotalInc / 4} times, end at row{" "}
          {(2 * upperBackTotalInc) / 4} (total {upperBackSts} sts).
        </li>
        <li>Work St sts till row {upperBackRows}</li>
      </ul>
      <h4>Dec for sleeve line</h4>
      <ul>
        <li>
          Row {upperBackRows + 1} (RS): Dec 1 sts on each side, k4, ssk, to last
          6 sts, k2tog, k4 (total {upperBackSts - 2} sts)
        </li>
        <li>
          Repeat every 2 rows {backDecRows[0]} times, every 4 rows,{" "}
          {backDecRows[1]} times (total {backDecSts} times), until row{" "}
          {afterDecRows} (total {upperBackSts - 2 * backDecSts} sts)
        </li>
        <li>Work St sts for {armInsetStraightRows} rows</li>
      </ul>
      <h4>Armhole inc</h4>
      <ul>
        <li>Row {afterDecRows + armInsetStraightRows + 1} (RS): Inc 1 sts</li>
        <li>
          Repeat every 2 rows {armholeIncRows[0]} times, then every 4 rows{" "}
          {armholeIncRows[1]} times (total{" "}
          {upperBackSts -
            2 * backDecSts +
            armholeIncRows[0] * 2 +
            armholeIncRows[1] * 2}{" "}
          sts)
        </li>
        <li>
          Row {totalUpperBackRows} (WS): total rows: {totalUpperBackRows} (about{" "}
          {getRound(totalUpperBackRows / rowGauge, 0.25)} inch),
        </li>
      </ul>
      <div>
        Remaining {ARM_INSET_STS * 2} sts ({ARM_INSET_STS} sts on each side)
        will be CO's when connected with front
      </div>
      <h2>Left shoulder</h2>
      <ul>
        <li>
          <strong>Pick up {pickupSts}</strong> sts
        </li>
        <li>Work {shoulderStraightRows} rows in St sts</li>
        <li style={{ color: "red" }}>
          <strong>
            {13 + 3 * 2} sts is the motif in the center; inc 5 sts to make mega
            braid in the first few rows; pm after 4 sts, then 5 sts before end
          </strong>
        </li>
      </ul>
      <h4>Shaping row</h4>
      <ul>
        <li>
          Row {shoulderStraightRows + 1} (RS): k3, M1L, k to last 5 sts, k2tog,
          k3
        </li>
        <li>
          Rep the sharping row every{" "}
          {getEven(shoulderShapeRows / shoulderShapeSts)} row, till row{" "}
          {shoulderStraightRows + shoulderShapeRows}
        </li>
      </ul>
      <h4>Inc rows</h4>
      <ul>
        <li>
          Row {shoulderStraightRows + shoulderShapeRows + 1} (RS): Inc 1 sts;
          k3, M1L, k to end
        </li>
        <li>
          Every 2 rows {neckLineRows[0]} times: row{" "}
          {Array.from({ length: neckLineRows[0] })
            .map((_, i) => shoulderStraightRows + shoulderShapeRows + 1 + i * 2)
            .join(", ")}{" "}
          (total {pickupSts + neckLineRows[0]} sts)
        </li>
        <li>
          Every 4 rows {neckLineRows[1]} times: row{" "}
          {Array.from({ length: neckLineRows[1] })
            .map(
              (_, i) =>
                shoulderStraightRows +
                shoulderShapeRows +
                1 +
                neckLineRows[0] * 2 +
                i * 4
            )
            .join(", ")}{" "}
          (total {pickupSts + neckLineRows[0] + neckLineRows[1]} sts)
        </li>
      </ul>
      <h4>Armhole inc</h4>
      <ul>
        <li>
          Do the same as the back side, at starting row{" "}
          {frontRows - armInsetStartRow}, end row {frontRows}
        </li>
        <li>
          Total{" "}
          {pickupSts +
            neckLineRows[0] +
            neckLineRows[1] +
            armholeIncRows[0] +
            armholeIncRows[1]}{" "}
          sts
        </li>
      </ul>
      <h3>Right shoulder</h3>
      <div>Do the reflected version of the left</div>
      <h2>Lower body</h2>
      <ul>
        <li>
          Row {frontRows + 1}, connect right shoulder, left shoulder, and upper
          back; CO {ARM_INSET_STS * 2} sts on each side of body; total {bodySts}{" "}
          sts
        </li>
        <li>
          Work {size.total_length - size.armhole_depth - size.hem_length} inch:{" "}
          {lowerBodyRows - 2} rows
        </li>
        <li>Dec evenly {getEven(bodySts * 0.08)} sts</li>
        <li>
          With smaller needle, p one row, then k1p1 for{" "}
          {getRound(size.hem_length * rowGauge * 1.1)} rows
        </li>
      </ul>
    </div>
  );
}
