import {
  BaseChart,
  getRawStsFromBaseRow,
  spliceRow,
  definition,
  getRound,
} from "@tanyoknits/shared";

const { empty, sr_rs, sr_ws, k, lli, rli, koco } = definition;

/** Line - used for shoulder drop
      Get the start count, then increase equally till the last row
      Returns sts count from the end, every other row
      */
export function getLineSRCount(stsCount: number, rowCount: number): number[] {
  const start = Math.round((stsCount / rowCount) * 2);
  const inc = Math.ceil((stsCount - start) / ((rowCount - 2) / 2));
  const lineStsCount: number[] = [];
  let sum = start;
  for (let row = 1; row < rowCount; row += 2) {
    lineStsCount.push(Math.max(stsCount - sum, 0)); // last row may have fewer inc
    sum += inc;
  }
  return lineStsCount;
}

export function shapeShoulderDrop(
  baseChart: BaseChart,
  shoulder: number,
  shoulderDrop: number,
  armInset: number = 0,
  isFront: boolean = false
): void {
  const backShoulderLine = getLineSRCount(shoulder, shoulderDrop);

  // First two rows: remove cables or other stitches into k/p stitches
  for (let i = 0; i < 2; i++) {
    const rawSts = getRawStsFromBaseRow(baseChart[i]);
    spliceRow(
      baseChart[i],
      0,
      rawSts.length,
      rawSts.map((s) => definition[s])
    );
  }
  // shoulder drop - right
  // offset before start; for front right
  const offset = isFront ? 2 : 0;
  for (let i = 0 + offset; i < shoulderDrop - 1 + offset; i++) {
    const sr = backShoulderLine[Math.ceil((i - offset) / 2) - 1] ?? 0;
    spliceRow(baseChart[i], armInset, sr);
    if (i > 0 && i % 2 === 1) {
      spliceRow(baseChart[i], armInset + sr - 1, 1, sr_rs);
    }
  }
  // shoulder drop - left
  for (let i = 0; i < shoulderDrop; i++) {
    const leftStartSts =
      armInset + (backShoulderLine[Math.floor(i / 2) - 1] ?? 0);
    spliceRow(baseChart[i], null, leftStartSts);
    if (i > 1 && i % 2 === 0) {
      spliceRow(baseChart[i], null, leftStartSts, [
        sr_ws,
        ...Array(leftStartSts - 1).fill(empty),
      ]);
    }
  }
}

/** Shaping */
export function getIncEveryXandYRows(
  rowCount: number,
  stsCount: number,
  everyNRow: number = 4
): {
  everyX: number;
  everyY: number;
  firstDecRow: number;
  secondDecRow: number;
  armCOAdj: number;
} {
  let a = everyNRow;
  let b = everyNRow - 2;
  function getDecCount(
    a: number,
    b: number
  ): {
    everyX: number;
    everyY: number;
    firstDecRow: number;
    secondDecRow: number;
    armCOAdj: number;
  } {
    const everyX = getRound((rowCount - b * stsCount) / (a - b));
    const everyY = stsCount - everyX;
    return {
      everyX,
      everyY,
      firstDecRow: a,
      secondDecRow: b,
      armCOAdj: b === 0 ? everyY : 0,
    };
  }
  const { everyX, everyY } = getDecCount(a, b);
  if (everyX < 0) {
    // If the bigger number is negative, reduce row numbers
    return getDecCount(a - 2, b - 2);
  } else if (everyY < 0) {
    // If the smaller number is negative, ignore and reduce every X
    return {
      everyX: everyX + everyY,
      everyY: 0,
      firstDecRow: a,
      secondDecRow: b,
      armCOAdj: 0,
    };
  } else {
    return { everyX, everyY, firstDecRow: a, secondDecRow: b, armCOAdj: 0 };
  }
}

// V neck shape
export function getVNeckShape(rowCount: number, stsCount: number): number[] {
  const incVals = getIncEveryXandYRows(rowCount, stsCount, 4);
  const { everyX, everyY } = incVals;
  const emptyStsCount = [];
  // Every 4 rows, X times
  for (let row = 1; row <= everyX; row++) {
    emptyStsCount.push(stsCount - row, stsCount - row);
  }
  // Every 2 rows, Y times
  for (let row = 1; row <= everyY; row++) {
    emptyStsCount.push(stsCount - everyX - row);
  }
  return emptyStsCount;
}

export function shapeNeckLine(
  baseChart: BaseChart,
  neck: number,
  shoulder: number,
  neckDepth: number,
  neckCurve: number[],
  gutter: number = 1 // sts count before inc
): void {
  const sideSR = neckCurve[0];

  for (let i = 0; i < neckDepth; i++) {
    const srStsRight = sideSR - neckCurve[Math.floor(i / 2)];
    const startSts = shoulder + srStsRight;
    const stStsLeft = sideSR - neckCurve[Math.max(0, Math.ceil(i / 2) - 1)]; // increase is in the next row of the right side
    const frontNeckCutCount = neck - srStsRight - stStsLeft;

    // Neck curve filled with empty sts
    spliceRow(baseChart[i], startSts, frontNeckCutCount);
    // Edge stitches are always knit
    spliceRow(baseChart[i], startSts - gutter, gutter, k);
    spliceRow(baseChart[i], startSts + frontNeckCutCount, gutter, k);

    // Right curve: show M1 & koco(+)
    if (i > 1 && i % 2 === 0) {
      const coSts =
        neckCurve[Math.floor((i - 2) / 2)] - neckCurve[Math.floor(i / 2)];
      // If there's increase, use M1
      spliceRow(
        baseChart[i],
        startSts - coSts - gutter,
        Math.min(1, coSts),
        lli
      );
      // If the increase is more than one sts, use koco
      if (coSts > 1) {
        spliceRow(
          baseChart[i],
          startSts - coSts + gutter,
          coSts - gutter,
          koco
        );
      }
    }
    // Left curve: show M1 & koco(+)
    if (i > 1 && i % 2 === 1) {
      const coSts =
        neckCurve[Math.max(0, Math.ceil((i - 2) / 2) - 1)] -
        neckCurve[Math.max(0, Math.ceil(i / 2) - 1)];
      // If there's increase, use M1
      spliceRow(
        baseChart[i - 1], // M1 is on the RS (previous row)
        startSts + frontNeckCutCount + coSts + (gutter - 1),
        Math.min(1, coSts),
        rli
      );
      // Add one k as there's M1
      spliceRow(
        baseChart[i - 1],
        startSts + frontNeckCutCount + coSts - 1,
        Math.min(1, coSts),
        k
      );
      // If the increase is more than one sts, use koco
      if (coSts > 1) {
        spliceRow(baseChart[i], startSts + frontNeckCutCount, coSts - 1, koco);
      }
    }
  }
}
