import {
  getChartFromText,
  getChartWithRepeat,
  getMergedChart,
  getRightLeftFlippedChart,
} from "../../utils";

import definition from "../definition";

const { c222r } = definition;

const filler = "p k:2 p:2 k:2 p:2 k:2 p";
const rightFave = [
  "p c4lh k:2 p:2 k:2 p",
  "p k:6 p:2 k:2 p",
  "p k:2 c22lp p:2 k:2 p",
];
const leftFave = [
  "p k:2 p:2 c4lh k:2 p",
  "p k:2 p:2 k:6 p",
  "p k:2 p:2 k:2 c22lp p",
];
const halfChart = getChartFromText([
  `${filler} * 2`,
  ...rightFave,
  filler,
  ...leftFave,
  `${filler} * 3`,
  ...leftFave,
  filler,
  ...rightFave,
  filler,
]);

const flippedChart = getRightLeftFlippedChart(halfChart);
const chartUnit = getMergedChart([
  halfChart,
  flippedChart,
  halfChart,
  flippedChart,
]);

const REPEAT_SSE = 15;

export const getMotif = (multiple: number = 1) => {
  const chart = getChartWithRepeat(chartUnit, multiple, [12, 12], 1);

  // Switch stitches into cable crossing columns

  if (multiple === 0) {
    chart[6][0].sts.splice(6, 6, c222r);
    chart[14][0].sts.splice(6, 6, c222r);
  }

  // crossing in the middle repeats
  for (let i = 0; i < multiple; i++) {
    chart[6][0].sts.splice(14 + 13 * i, 6, c222r);
    chart[14][0].sts.splice(14 + 13 * i, 6, c222r);

    chart[4][0].sts.splice(5 + 13 * i, 6, c222r);
    chart[16][0].sts.splice(5 + 13 * i, 6, c222r);
    if (i === multiple - 1) {
      chart[4][0].sts.splice(-11, 6, c222r);
      chart[16][0].sts.splice(-11, 6, c222r);
    }
  }

  return {
    title: "Fusion",
    chart,
    stsCount: 24 * multiple + 22 + (multiple === 0 ? 2 : 0),
    stEquivCount: REPEAT_SSE * multiple + 15 + (multiple === 0 ? 2 : 0),
  };
};
