import { getChartFromText } from "../../utils/chartModifier";
import { Motif } from "../../utils/types";
import { getRound } from "../../utils";

export const getMotif = (
  stsCount: 6 = 6,
  rowCount: 12 | 8 | 6 | 4 | 2 = 8,
  direction: "left" | "right" = "left",
  rowOffset: number = 0
): Motif => {
  let cable = null;
  switch (stsCount) {
    // Add case later as you make more cables
    default:
      cable = direction === "right" ? "c222r" : "c222l";
  }

  const kCount = (stsCount - 2) / 2;
  const fillerRow = `k:${kCount} p:2 k:${kCount}`;
  const chartText = [cable, `${fillerRow} * ${rowCount - rowOffset - 1}`];
  if (rowOffset > 0) {
    chartText.unshift(`${fillerRow} * ${rowOffset}`);
  }

  return {
    title: "Cable with purl in the middle",
    chart: getChartFromText(chartText),
    stsCount,
    stEquivCount: getRound(((stsCount * 9) / 10) * 1000, 250) / 1000,
  };
};
