import React from "react";
import { projects } from "./projects";
import DoyoSweater from "./DoyoSweater";
import HerringboneVest from "./HerringboneVest";
import StripeVest from "./StripeVest";
import BasicVest from "./BasicVest";
import VVest from "./VVest";
import MegaBraidVest from "./MegaBraidVest";
import CableBalaklava from "./CableBalaklava";

export default function Project({ projectID }: { projectID: number }) {
  const project = projects[projectID];
  return (
    <div>
      {projectID === 0 && (
        <DoyoSweater gauge={project.gauge} size={project.sizes[0]} />
      )}
      {projectID === 1 && (
        <HerringboneVest gauge={project.gauge} size={project.sizes[0]} />
      )}
      {projectID === 2 && (
        <StripeVest gauge={project.gauge} size={project.sizes[0]} />
      )}
      {projectID === 3 && (
        <BasicVest gauge={project.gauge} size={project.sizes[0]} />
      )}
      {projectID === 4 && (
        <VVest gauge={project.gauge} size={project.sizes[0]} />
      )}
      {projectID === 5 && (
        <MegaBraidVest gauge={project.gauge} size={project.sizes[0]} />
      )}
      {projectID === 6 && (
        <CableBalaklava gauge={project.gauge} size={project.sizes[0]} />
      )}
    </div>
  );
}
