import { getChartFromText, getChartWithRepeat } from "../../utils";

const filler = "k:2 p:2 k:2 p:2 k:2 p:2";
const toFave = [
  "k:2 p:2 k:2 c4rh p:2",
  "k:2 p:2 k:6 p:2",
  "k:2 p:2 c22rp k:2 p:2",
];
const fromFave = [
  "c4lh k:2 p:2 k:2 p:2",
  "k:6 p:2 k:2 p:2",
  "k:2 c22lp p:2 k:2 p:2",
];
const chartUnit = getChartFromText([
  ...toFave,
  `${filler} * 3`,
  ...toFave,
  filler,
  ...fromFave,
  `${filler} * 3`,
  ...fromFave,
  filler,
]);

const REPEAT_SSE = 8.5;

export const getMotif = (multiple: number = 1) => {
  return {
    title: "To & Fro Fave",
    chart: getChartWithRepeat(chartUnit, multiple),
    stsCount: 12 * multiple,
    stEquivCount: REPEAT_SSE * multiple,
  };
};
