import {
  getChartFromText,
  getChartWithRepeat,
  getMergedChart,
  getRightLeftFlippedChart,
} from "../../utils";

const filler = "p k:2 p:2 k:2 p:2 k:2 p";
const rightFave = [
  "p c4lh k:2 p:2 k:2 p",
  "p k:6 p:2 k:2 p",
  "p k:2 c22lp p:2 k:2 p",
];
const leftFave = [
  "p k:2 p:2 c4lh k:2 p",
  "p k:2 p:2 k:6 p",
  "p k:2 p:2 k:2 c22lp p",
];
const halfChart = getChartFromText([
  ...rightFave,
  `${filler} * 3`,
  ...rightFave,
  filler,
  ...leftFave,
  `${filler} * 3`,
  ...leftFave,
  filler,
]);

const flippedChart = getRightLeftFlippedChart(halfChart);
const chartUnit = getMergedChart([
  halfChart,
  flippedChart,
  halfChart,
  flippedChart,
]);

const REPEAT_SSE = 15;

export const getMotif = (
  multiple: number = 1,
  showRepeatInColor: boolean = false
) => {
  return {
    title: "Incline Fave",
    chart: getChartWithRepeat(
      chartUnit,
      multiple,
      [12, 12],
      1,
      showRepeatInColor
    ),
    stsCount: 24 * multiple + 22,
    stEquivCount: REPEAT_SSE * multiple + 15,
  };
};
