import {
  getBaseRowFromChartRow,
  getChartFromText,
  getChartWithRepeat,
  getRightLeftFlippedChart,
  Motif,
} from "../../utils";
import definition from "../definition";

const filler = "k:4 p:2 k:4 p:2 k:4";
const chartUnit = getChartFromText([
  `${filler} * 2`,
  "k:4 p:2 c4r p:2 k:4",
  filler,
  "k:4 c4rh c4lh k:4",
  "k:16",
  "k:2 c22rp k:4 c22lp k:2",
  filler,
  "c4r p:2 k:4 p:2 c4r",
  `${filler} * 3`,
  "c4r p:2 k:4 p:2 c4r",
  filler,
  "k:2 c4lh k:4 c4rh k:2",
  "k:16",
  "k:4 c22lph c22rph k:4",
  filler,
  "k:4 p:2 c4r p:2 k:4",
  filler,
]);

const REPEAT_STS = 12;
const REPEAT_SSE = 8.5;

export const getMotif = (
  multiple: number = 1,
  isRightLeftFlipped: boolean = false
): Motif => {
  const chart = getChartWithRepeat(chartUnit, multiple, [2, 2]);
  const emptyStsPoint = [2, 8, 14].flatMap((n) =>
    Array.from({ length: multiple }, (_, i) => n + i * REPEAT_STS)
  );
  const baseStEquivRow = getBaseRowFromChartRow(chart[0]).map((s, i) =>
    emptyStsPoint.includes(i) ? definition.empty : s
  );
  return {
    title: "Linked cable",
    chart: isRightLeftFlipped ? getRightLeftFlippedChart(chart) : chart,
    stsCount: REPEAT_STS * multiple + 4,
    stEquivCount: REPEAT_SSE * multiple + 3.5,
    baseStEquivRow: isRightLeftFlipped
      ? baseStEquivRow.reverse()
      : baseStEquivRow,
  };
};
