import {
  Chart,
  getChartFromText,
  getRightLeftFlippedChart,
  Motif,
} from "../../utils";

const twistR = "p k p c2r k p k p:2 k p c2r k p k p";
const base = "p k p k:3 p k p:2 k p k:3 p k p";

const twistRChart = getChartFromText([twistR, base]);
const twistLChart = getRightLeftFlippedChart(twistRChart);
const centerCableChart = getChartFromText(["c18l", base]);

const chart: Chart = [
  twistRChart,
  twistLChart,
  twistRChart,
  twistLChart,
  twistRChart,
  twistLChart,
  centerCableChart,
  twistLChart,
  twistRChart,
  twistLChart,
  twistRChart,
  twistLChart,
].flat();

export const getMotif = (): Motif => {
  return {
    title: "Braid Mega",
    chart,
    stsCount: 18,
    stEquivCount: 12.5,
    notes:
      "Center cable: slip 9 sts to cn, hold to front, p1, k1, p1, RT, [k1, p1] twice, (p1, k1, p1, RT, [k1, p1] twice) from cn",
  };
};
