import React, { useState } from "react";
import { projects } from "./projects";
import Project from "./Project";

function App() {
  const [projectID, setProjectID] = useState<number>(6);

  return (
    <div>
      <div>
        {projects.map((project, i) => (
          <span
            key={i}
            style={{
              marginRight: 16,
              cursor: "pointer",
              textDecoration: projectID === i ? "none" : "underline",
              fontWeight: projectID === i ? "bold" : "normal",
            }}
            onClick={() => setProjectID(i)}
          >
            {project.name}
          </span>
        ))}
      </div>
      <Project projectID={projectID} />
    </div>
  );
}

export default App;
