import { getChartFromText, getRightLeftFlippedChart } from "../../utils";
import { Motif } from "../../utils/types";
import { getChart } from "./Pyramid";

const chart = getChartFromText([
  "k2tog k:2 rli k:3",
  "k:7",
  "k2tog k:2 rli k:3",
  "k:7",
]);

const row1Repeat = "k2tog k:2 rli k:3";
const row3Repeat = "k:2 rli k:3 k2tog";

const baseStsCount = 7;

export const getMotif = (
  repeat: number = 1,
  isFlipped: boolean = false
): Motif => {
  const chart = getChartFromText([
    `${Array(repeat).fill(row1Repeat).join(" ")} k`,
    `k:${repeat * baseStsCount + 1}`,
    `k ${Array(repeat).fill(row3Repeat).join(" ")}`,
    `k:${repeat * baseStsCount + 1}`,
  ]);
  return {
    title: "Herringbone",
    chart: isFlipped ? getRightLeftFlippedChart(chart) : chart,
    stsCount: repeat * baseStsCount + 1,
    notes:
      "Round 3: repeat from k2 to last 6 sts, k2 RLI, k3, k2tog (last sts of round together with the first st of next rnd)",
  };
};
