import { getChartFromText } from "../../utils/chartModifier";
import { Motif } from "../../utils/types";

export const getMotif = (isOFirst: boolean = false): Motif => {
  const o = ["k:8 * 2", "c4r c4l", "k:8 * 3", "c4l c4r", "k:8"];
  const x = ["k:8 * 2", "c4l c4r", "k:8 * 3", "c4r c4l", "k:8"];
  return {
    title: "XO cable",
    chart: getChartFromText(isOFirst ? [...o, ...x] : [...x, ...o]),
    stsCount: 8,
    stEquivCount: 6,
  };
};
